import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import RemoveRedEyeOutlinedIcon from '@mui/icons-material/RemoveRedEyeOutlined';
import { Formik } from 'formik';
import { Button } from '@mui/material';
import MarkSvg from 'SvgComponents/MarkSVG/MarkSvg';
import { signUp } from '../../redux/auth/thunk';
import {
  BoxEye,
  ContainerForm,
  ErrorMessage,
  Field,
  FieldPassword,
  Form,
  Link,
  View,
  socialSingInButton,
} from './FormRegister.styled';

export default function FormRegister() {
  const [visible, setVisible] = useState(false);

  const dispatch = useDispatch();
  function onToggleView() {
    setVisible(prev => !prev);
  }

  return (
    <ContainerForm>
      <Formik
        initialValues={{
          firstName: '',
          lastName: '',
          numberPhone: '',
          email: '',
          password: '',
        }}
        validateOnChange={false}
        validateOnBlur={false}
        validate={values => {
          const errors = {};

          if (!values.firstName) {
            errors.firstName = "' ";
          } else if (
            values.firstName.length < 2 ||
            values.firstName.length > 20
          ) {
            errors.firstName = '   2  20 ';
          } else if (
            values.firstName.slice(0, 1) ===
            values.firstName.slice(0, 1).toLowerCase()
          ) {
            errors.firstName = '    ';
          }

          if (!values.lastName) {
            errors.lastName = "' ";
          } else if (
            values.lastName.length < 2 ||
            values.lastName.length > 20
          ) {
            errors.lastName = '   2  20 ';
          } else if (
            values.lastName.slice(0, 1) ===
            values.lastName.slice(0, 1).toLowerCase()
          ) {
            errors.lastName = '    ';
          }

          if (!values.numberPhone) {
            errors.numberPhone = "' ";
          } else if (
            !/^((\+38)[- ]?)(\(?(0)\d{2}\)?[- ]?)?\d{2}[- ]?\d{1}[- ]?\d{1}[- ]?\d{1}[- ]?\d{2}$/i.test(
              values.numberPhone
            )
          ) {
            errors.numberPhone = ' ';
          }

          if (!values.email) {
            errors.email = "' ";
          } else if (
            !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
          ) {
            errors.email = ' email ';
          }
          if (!values.password) {
            errors.password = "' ";
          } else if (
            !/^(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*()_+={};:'",.<>?/\\[\]|]).{6,20}$/.test(
              values.password
            )
          ) {
            errors.password = ' ';
          }
          return errors;
        }}
        onSubmit={values => {
          dispatch(signUp(values));
        }}
      >
        {({ values, isSubmitting, handleChange, setSubmitting }) => (
          <Form>
            <label>
              <p>
                <span className="label-place">
                  I`
                  <MarkSvg />
                </span>
              </p>
              <Field
                type="text"
                name="firstName"
                value={values.firstName.trim()}
                onChange={e => {
                  handleChange(e);
                  setSubmitting(false);
                }}
              />
              <ErrorMessage name="firstName" component="p" />
            </label>
            <label>
              <p>
                <span className="label-place">
                  i
                  <MarkSvg />
                </span>
              </p>
              <Field
                type="text"
                name="lastName"
                value={values.lastName.trim()}
                onChange={e => {
                  handleChange(e);
                  setSubmitting(false);
                }}
              />
              <ErrorMessage name="lastName" component="p" />
            </label>
            <label>
              <p>
                <span className="label-place">
                   
                  <MarkSvg />
                </span>
              </p>
              <Field
                type="tel"
                name="numberPhone"
                placeholder="+380 (__) ___-__-__"
                value={values.numberPhone.trim()}
                onChange={e => {
                  handleChange(e);
                  setSubmitting(false);
                }}
              />
              <ErrorMessage name="numberPhone" component="p" />
            </label>
            <label>
              <p>
                <span className="label-place">
                   
                  <MarkSvg />
                </span>
              </p>
              <Field
                type="email"
                name="email"
                placeholder="tanya@example.com"
                value={values.email.trim()}
                onChange={e => {
                  handleChange(e);
                  setSubmitting(false);
                }}
              />
              <ErrorMessage name="email" component="p" />
            </label>
            <label>
              <p>
                <span className="label-place">
                  
                  <MarkSvg />
                </span>
              </p>
              <FieldPassword>
                {visible ? (
                  <Field
                    type="text"
                    name="password"
                    value={values.password.trim()}
                    onChange={e => {
                      handleChange(e);
                      setSubmitting(false);
                    }}
                  />
                ) : (
                  <Field
                    type="password"
                    name="password"
                    value={values.password.trim()}
                    onChange={e => {
                      handleChange(e);
                      setSubmitting(false);
                    }}
                  />
                )}
                <BoxEye onClick={onToggleView} type="button">
                  {visible && <View></View>}
                  <RemoveRedEyeOutlinedIcon />
                </BoxEye>
              </FieldPassword>
              <ErrorMessage name="password" component="p" />
            </label>
            <p className="rules">
                  6-20 ,   ,
               ,   
            </p>
            <p>
              ,   {' '}
              <Link to="#"> </Link> {' '}
              <Link to="#"> </Link>
            </p>
            <Button
              type="submit"
              sx={socialSingInButton}
              disabled={isSubmitting}
            >
              
            </Button>
          </Form>
        )}
      </Formik>
    </ContainerForm>
  );
}
